body, h1, h2, h3, ul, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

body {
  color: #B5B5B5;
  min-width: 640px;
}

tot-header div#header {
  height: 430px;
  background: url('bg1.png') repeat-y;
  padding-top: 1px;
}

tot-header div#header h1#logo {
  background: url('logo.png') no-repeat 0 0/100% 100%;
  mix-blend-mode: lighten;
  height: 50px;
  width: 150px;
  margin: 80px auto 0;
  font-size: 0;
}

tot-header div#header tot-search {
  width: 90.4%;
  max-width: 655px;
  display: block;
  margin: 80px auto 0;
  position: relative;
}

tot-header div#header tot-search input[type=text],
tot-header div#header tot-search button.submit,
tot-header div#header tot-search ul li {
  display: inline-block;
  vertical-align: bottom;
  height: 70px;
  text-indent: 30px;
  border: 0;
  outline: none;
  padding: 0;
}

tot-header div#header tot-search input[type=text]:focus {
  background-color: #FAFAFA;
}

tot-header div#header tot-search input[type=text] {
  width: calc( 100% - 110px);
  font-size: 24px;
  text-transform: uppercase;
  color: #B5B5B5;
}

tot-header div#header tot-search button.submit {
  width: 110px;
  text-indent: 0;
  background-color: #FCDB67;
}

tot-header div#header tot-search button.submit fa-icon {
  color: #FFF;
  font-size: 30px;
}

tot-header div#header tot-search ul {
  width: 100%;
  position: absolute;
  list-style-type: none;
  z-index: 1;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
}

tot-header div#header tot-search ul li {
  width: inherit;
  background-color: #FFF;
  height: auto;
  padding: 21px 0;
  font-weight: 900;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 24px;
  text-transform: uppercase;
  color: #B5B5B5;
}

tot-header div#header div.wave-bar {
  background: url('wave-bar.png') no-repeat 0 0/100% 100%;
  height: 83px;
  margin-top: 94px;
  mix-blend-mode: lighten;
}

tot-search-results {
  background-color: #FAFAFA;
  height: 520px;
  display: block;
  padding-top: 1px;
  margin-top: 18px;
}

tot-search-results div#search-results {
  margin-top: 16px;
}

tot-search-results div#search-results div.clean {
  margin: 120px auto 0;
}

tot-search-results div#search-results div.clean img {
  display: block;
  height: 160px;
  width: 160px;
  margin: 0 auto;
}

tot-search-results div#search-results div.clean p {
  width: 230px;
  margin: 30px auto 0;
  font-size: 24px;
  text-align: center;
}

tot-search-results div#search-results ul {
  width: 90%;
  min-width: 610px;
  max-width: 786px;
  list-style-type: none;
  margin: 0 auto;
}

tot-search-results div#search-results tot-search-result {
  display: block;
  background-color: #FFF;
  margin-bottom: 30px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
}

tot-search-results div#search-results tot-search-result .title {
  padding: 25px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

tot-search-results div#search-results tot-search-result .title img, tot-search-results div#search-results tot-search-result .title h3 {
  display: inline-block;
  vertical-align: bottom;
}

tot-search-results div#search-results tot-search-result .title img {
  width: 150px;
}

tot-search-results div#search-results tot-search-result .title h3 {
  width: 200px;
  margin-left: 30px;
  text-transform: uppercase;
}

tot-search-results div#search-results tot-search-result .title a {
  cursor: default;
  float: right;
  color: #7429FF;
  margin-top: 3px;
}

tot-search-results tot-search-result .description {
  padding: 30px 25px;
}

tot-search-results tot-search-result .description p, tot-search-results tot-search-result .description button {
  display: inline-block;
  vertical-align: bottom;
}

tot-search-results tot-search-result .description p {
  height: 50px;
  width: 410px;
  text-indent: 15px;
  color: #525252;
}

tot-search-results tot-search-result .description button {
  padding: 14px 25px;
  background-color: #FCDB67;
  border: 0;
  text-transform: uppercase;
  color: #FFF;
  font-size: 20px;
  font-weight: 600;
  float: right;
}

tot-results-modal div#lightbox {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

tot-results-modal div.modal {
  height: 90px;
  width: 260px;
  margin: 280px auto 0;
  padding: 45px 95px;
  background-color: #FFF;
  font-weight: 600;
}

tot-results-modal p {
  margin-bottom: 36px;
  font-size: 16px;
}

tot-results-modal button {
  background-color: transparent;
  text-transform: uppercase;
  font-size: 18px;
  border: 0;
  color: #7429FF;
  outline: none;
}

tot-results-modal button:nth-of-type(1) {
  margin-left: 56px;
}

tot-results-modal button:nth-of-type(2) {
  margin-left: 50px;
}
